@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    /* background-color: #2c2c2c; */
    background-color: azure;
}
section{
    padding: 10px 0;
    background-color: #070707;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    opacity: 0.9;
    border-bottom: 2px solid rgb(83.139038%,61.569214%,25.099182%);
}
ul{
    display: flex;
    flex-direction: row;
    justify-content:center;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}
li{
    list-style-type: none;
    max-width: 33%;
    margin: 0 5vw 0 5vw;
    font-family: 'Poppins', sans-serif;
    /* font-family: 'Indie Flower', cursive; */
    font-size: 1.2em;
    font-weight: 600;
    letter-spacing: 1px;
}

li a,
li a:visited{
    color: rgb(83.139038%,61.569214%,25.099182%);
    text-decoration: none;
}
li:hover,
li a:active,
li a:hover{
    color:rgb(83.139038%,61.569214%,25.099182%);
}
li a:active,
li a:hover,
li a:visited{
    text-decoration: underline;
}
section{
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
}
:root{
        --shadow: inset 0px 0px 49px 31px rgba(0,0,0,0.93);
}
